import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import ElementUI from 'element-ui';
import config from '../vue.config';
import 'element-ui/lib/theme-chalk/index.css';
const bus = new Vue();
Vue.prototype.$bus = bus

import Axios from 'axios'
Vue.config.productionTip = false
    // step2axiosvuevueaxios,
    //  import axios $axios 
Vue.prototype.$axios = Axios;
// step3 /api   // https://89skins.com/
// Axios.defaults.baseURL = 'https://t.ym4954.armin.cc/';   // '/api'   https://ahyltt.com.com/
Axios.defaults.baseURL = 'http://www.luckykx.com/'; // '/api'   https://ahyltt.com.com/
// Axios.defaults.baseURL = '/'
// Axios.defaults.baseURL = config.target;
Axios.defaults.timeout = 60000;
Axios.defaults.retry = 3;




import qs from 'qs';
// Vue.prototype.$ajax = axios // axios$ajax
Vue.prototype.$qs = qs;

//
Vue.directive('title', {
    inserted: function(el, binding) {
        document.title = el.dataset.title
    }
})


Vue.config.productionTip = false
Vue.use(ElementUI);

new Vue({
    router,
    store,
    render: function(h) { return h(App) }
}).$mount('#app')